.radio-question, .text-input-question, .switch-question, .slider-question{
    justify-self: center;
    border: solid;
    border-width: 1px;
    width: 70%;
    text-align: left;
    padding: 10px 10px 10px 10px;
    border-radius: 15px;
    background-color: #fbf6f2;
    border-width: 10px;
    border-color: #d6844b;
    box-shadow: 0px 0px 10px #0249325c;
    margin-bottom: 30px;
    margin-top:30px;

}


.question-number{
  display: flex;
  background-color: #f8eed4;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 100%;
  justify-self: center;
  margin: 10px 0px 10px 0px;
  padding: 10px 10px 10px 10px;
  text-align: left;
  justify-content: space-between;
  color: #d6844b;
}

.question-text{
  margin: 0px 10px 10px 10px;
  font-family: Creato;
  font-size: large;
}

.radio-choices{
    margin: 0px 10px 0px 10px;
}

#more-info{
    height:20px
}

#img-holder{
    display: flex;
    align-items: center;
}

.tooltip {
    position: relative;
    display: inline-block;
    visibility: visible;
    opacity: 1;
    z-index: 0;
}
  
  /* Tooltip text */
.tooltiptext {
  visibility: hidden;
  width: 220px;
  color: #000000;
  text-align: center;
  padding: 5px ;
  border-radius: 6px;
  opacity: 1;
  transition: opacity .3s;
  background-color: rgb(247, 251, 255);
  bottom: 102%;
  left:-100px;
  
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}
  
  /* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: .95;
}

.form-range{
    color:blue
}

.slider-container::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background-color:  #266d56; /* <-- change this to your desired color */
  border-radius: 50%;
  cursor: pointer;
  border: none;
  margin-top: -7px; /* align with track height */
  box-shadow: none;
}

.slider-container::-webkit-slider-runnable-track {
  height: 6px;
  background: #b7dea9; /* <-- change this to your desired track color */
  border-radius: 3px;
}


.form-range {

  &:focus {
    outline: 0;

    &::-webkit-slider-thumb { box-shadow: none }
  }
}

.form-check-input{
    background-color: #ffffff;
    box-shadow: none;
}

.form-check-input:checked{
    background-color: #266d56;
}

.form-check-input:focus{
    box-shadow: none;
}

#basic-container{
  text-align: center;
}

.form-label{
  text-align: left;
}
